import { NUM_PLAYERS } from "@hervan/swarms";
import * as AI from "boardgame.io/ai";
import { Local } from "boardgame.io/multiplayer";
import { Client } from "boardgame.io/react";
import { Board } from "./Board";
import { SwarmsGame } from "./Swarms";

const App = (seed: string) =>
  Client({
    game: { ...SwarmsGame, seed },
    board: Board,
    numPlayers: NUM_PLAYERS,
    multiplayer: Local({
      bots: {
        // 0: AI.RandomBot,
        1: AI.RandomBot,
        2: AI.RandomBot,
        3: AI.RandomBot,
      },
    }),
    debug: false,
  });

export default App;
