import { partition } from "@hervan/utils";
import { SwarmsState } from "index";
import { Action, Selection, noSelection } from "../Action";
import { Coordinates, TerrainMap, startCorners } from "../Map";
import { TerrainType, resourceStructure, resourceValues } from "../Map/Terrain";
import { Track } from "../Track";
import { TechGrid, blueprintMastery, playerStartingGrid } from "./Grid";
import { scienceScore } from "./Grid/Slot";
import { Species, SpeciesList } from "./Species";

export type Player = {
  id: number;
  species: Species;
  corner: Coordinates;
  timeSpent: number;
  swarm: Coordinates[];
  city: Coordinates[];
  selection: Selection<Action>;
  grid: TechGrid;
  delivery: number;
  silo: TerrainType[];
  treasury: TerrainType[];
  score: number;
};

export const newPlayer: (id: number) => Player = (id) => ({
  id,
  species: SpeciesList[id],
  corner: startCorners()[id],
  timeSpent: 0,
  swarm: [],
  city: [],
  selection: noSelection,
  grid: playerStartingGrid(),
  delivery: 0,
  silo: [],
  treasury: [],
  score: 0,
});

export const resourceScore = (player: Player) =>
  [...player.silo, ...player.treasury].reduce(
    (acc, r) => acc + resourceValues(player.id, r),
    0
  );

export const playerScore = (G: SwarmsState, player: Player) =>
  scienceScore(player, G.map).reduce((acc, score, i) => {
    const bpm = blueprintMastery(
      player.grid,
      player.grid[i][Action.Research].blueprint
    );
    const res = score * bpm;
    return acc + res;
  }, resourceScore(player));

export const sortResources = (G: SwarmsState, player: Player) => {
  const [treasury, silo] = partition(
    [...player.silo, ...player.treasury],
    (r) => r === resourceStructure(player.id).rare
  );
  return {
    ...player,
    silo: [...silo]
      .sort(
        (a, b) => resourceValues(player.id, a) - resourceValues(player.id, b)
      )
      .slice(-player.city.length),
    treasury,
    score: playerScore(G, player),
  };
};

const tiebreaker = (p1: Player, p2: Player, map: TerrainMap) =>
  p1.score > p2.score
    ? p1
    : p1.score < p2.score
    ? p2
    : Math.max(...scienceScore(p1, map)) > Math.max(...scienceScore(p2, map))
    ? p1
    : p2;

export const winner = (players: Player[], map: TerrainMap) =>
  players.reduce((p1, p2) => tiebreaker(p1, p2, map));

export const nextPlayer = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) => nextPlayers(players, turn, gameEndTriggered)[0];

export const nextPlayers = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) =>
  turn[
    players.reduce(
      (acc, { timeSpent }) => Math.min(timeSpent, acc),
      gameEndTriggered || players[0].timeSpent
    )
  ];

export * as Grid from "./Grid";
export * as Species from "./Species";

