import * as Swarms from "@hervan/swarms";
import { deepAssign, numberlist } from "@hervan/utils";
import type { Game, Move } from "boardgame.io";
import { INVALID_MOVE } from "boardgame.io/core";

type Action = Swarms.Action.Action;
type Blueprint = Swarms.Blueprint.Blueprint;
type Coordinates = Swarms.Map.Coordinates;
type TerrainMap = Swarms.Map.TerrainMap;
type Player = Swarms.Player.Player;
type Track = Swarms.Track.Track;

const { Action, ActionList, noSelection } = Swarms.Action;
const {
  BlueprintList,
  getAllBlueprintCoordinatesWithTranslation,
  validateBuildBlueprint,
  validateManufactureBlueprint,
  validateDeliveryBlueprint,
  validateSpreadingBlueprint,
} = Swarms.Blueprint;
const {
  GAME_SIZE,
  VECTOR_ORIENTATIONS,
  addCoordinates,
  applyMapEffect,
  containsCoord,
  getAdjacentToSelection,
  newMap,
  raiseMap,
  sameCoordinates,
  smoothMap,
  startCorners,
  harvest,
} = Swarms.Map;
const { newMarket, refillMarketTokens } = Swarms.Market;
const {
  newPlayer,
  nextPlayer,
  winner,
  playerScore,
  resourceScore,
  sortResources,
} = Swarms.Player;
const {
  isValidActionSelection,
  upgradeGrid,
  upgradeGameGrid,
  blueprintMastery,
} = Swarms.Player.Grid;
const { getActionRunner, scienceScore } = Swarms.Player.Grid.Slot;
const { moveOnTurnTrack } = Swarms.Track;

export const GAME_LENGTH = 2;

export type SwarmsState = Swarms.SwarmsState;

const playerSetupCity: (
  G: SwarmsState,
  blueprints: Blueprint[],
  id: number
) => SwarmsState = (G, blueprints, id) => {
  const currentPlayer = G.players[id];
  const grid0 = upgradeGrid(
    currentPlayer,
    currentPlayer.grid[0][Action.Spread],
    [blueprints[0]]
  );
  const grid1 = upgradeGrid(
    { ...currentPlayer, grid: grid0 },
    grid0[1][Action.Spread],
    [blueprints[1]]
  );
  const grid = upgradeGrid(
    { ...currentPlayer, grid: grid1 },
    grid1[0][Action.Build],
    [blueprints[1]]
  );
  const disposableCritters: Coordinates[] = getSpreadingPatterns(
    G.map,
    blueprints[1],
    currentPlayer
  ).sort((a, b) => sortDistanceToCorner(id, b, a))[0];
  const initialCritters = getAdjacentToSelection(disposableCritters);
  const swarm0 = [...initialCritters, ...disposableCritters];
  const map0 = applyMapEffect(G.map, swarm0, {
    owner: id,
    state: "occupied",
  });
  const player0 = {
    ...currentPlayer,
    grid,
    swarm: swarm0,
  };
  const building = getBuildPatterns(map0, blueprints[1], player0)[0];
  const mapBuildings = applyMapEffect(map0, building, {
    owner: id,
    state: "built",
    blueprint: blueprints[1],
  });
  const city = building;
  const swarm = getAdjacentToSelection(city);
  const map = applyMapEffect(mapBuildings, swarm, {
    owner: id,
    state: "occupied",
  });
  const player = { ...currentPlayer, grid, city, swarm };
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const playerSetupState: (
  G: SwarmsState,
  blueprints: Blueprint[]
) => SwarmsState = (G, blueprints) =>
  G.players.reduce(
    (acc, _, i) => playerSetupCity(acc, blueprints.slice(i, i + 2), i),
    G
  );

const triggerGameEnd = (G: SwarmsState, id: number) =>
  G.gameEndTriggered === undefined &&
  nextPlayer(G.players, G.turnTrack) !== id &&
  G.players.some(
    (p) =>
      p.grid.filter((row) => row[Action.Research].blueprint !== undefined)
        .length >= GAME_LENGTH
  );

const passTime: (G: SwarmsState, id: number) => SwarmsState = (G, id) => {
  const currentPlayer = G.players[id];
  const timeSpent = currentPlayer.timeSpent + currentPlayer.selection.time;
  const turnTrack = moveOnTurnTrack(
    G.turnTrack,
    currentPlayer.timeSpent,
    timeSpent
  );

  const player = { ...currentPlayer, timeSpent };

  const nextG = {
    ...G,
    players: G.players.map((p) => (p.id === currentPlayer.id ? player : p)),
    turnTrack,
  };

  if (triggerGameEnd(nextG, currentPlayer.id)) {
    return { ...nextG, gameEndTriggered: timeSpent };
  }
  return nextG;
};

const draftToken: Move<SwarmsState> = ({ G, playerID }, i: number) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const drawTokenGame = deepAssign(
    G,
    {
      ...currentPlayer,
      selection: { ...noSelection, stall: i },
    },
    "players",
    (p) => p.id === id
  );
  return drawTokenGame;
};

const selectSlot: Move<SwarmsState> = (
  { G, playerID },
  row: number,
  action: Action
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const stall = currentPlayer.selection.stall;
  if (stall === undefined) {
    return invalidMove();
  }
  const token = G.market.stalls[stall];
  const slot = currentPlayer.grid[row][action];
  if (
    isValidActionSelection(
      currentPlayer,
      slot,
      G.market.stalls.filter((t) => t === token)
    )
  ) {
    const selection = {
      ...currentPlayer.selection,
      slot,
    };
    const selectedSlotG = deepAssign(
      G,
      { ...currentPlayer, selection },
      "players",
      (p) => p.id === id
    );
    if (action === Action.Research) {
      return confirm(selectedSlotG, id);
    }
    return selectedSlotG;
  }
  return invalidMove();
};

const selectLocation: Move<SwarmsState> = (
  { G, playerID },
  coords: Coordinates[],
  action: Action
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const stall = currentPlayer.selection.stall;
  if (stall === undefined) {
    return invalidMove();
  }
  const token = G.market.stalls[stall];
  if (containsCoord(currentPlayer.selection.location, coords[0])) {
    return deepAssign(
      G,
      currentPlayer.selection.location.filter(
        (c) => !sameCoordinates(c, coords[0])
      ),
      "players",
      (p) => p.id === id,
      "selection",
      "location"
    );
  }
  const location = [...currentPlayer.selection.location, ...coords];
  const selection = {
    ...currentPlayer.selection,
    location,
  };
  const validation =
    action === Action.Spread
      ? validateSpreadingBlueprint
      : action === Action.Build
      ? validateBuildBlueprint
      : action === Action.Trade
      ? validateManufactureBlueprint
      : () => false;
  const valid = validation(G.map, selection.location, token, currentPlayer);
  if (valid) {
    if (location.length === 4 && action !== Action.Trade) {
      const validG = deepAssign(
        G,
        { ...selection, location: [], destination: [...selection.location] },
        "players",
        (p) => p.id === id,
        "selection"
      );
      return confirm(validG, id);
    }
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    return validG;
  }
  return invalidMove();
};

const deselectLocation: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.location.filter((c) => !sameCoordinates(c, coord)),
    "players",
    (p) => p.id === id,
    "selection",
    "location"
  );
};

const selectDestination: Move<SwarmsState> = (
  { G, playerID },
  coords: Coordinates[]
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const stall = currentPlayer.selection.stall;
  if (stall === undefined) {
    return invalidMove();
  }
  const token = G.market.stalls[stall];
  const destination = [...currentPlayer.selection.destination, ...coords];
  if (
    validateDeliveryBlueprint(
      G.map,
      token,
      currentPlayer.selection.location,
      destination,
      id
    )
  ) {
    const selection = {
      ...currentPlayer.selection,
      destination,
    };
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    if (destination.length === 4) {
      return confirm(validG, id);
    }
    return validG;
  }
  return invalidMove();
};

const deselectDestination: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.destination.filter(
      (c) => !sameCoordinates(c, coord)
    ),
    "players",
    (p) => p.id === id,
    "selection",
    "destination"
  );
};

const passTurn: Move<SwarmsState> = ({ G, events }) => {
  events.endTurn();
  return G;
};

const debugRaiseMap: Move<SwarmsState> = ({ G, random }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const raisedTerrains = raiseMap(originalTerrains, random.Number, 1);
  const improvedMap = raisedTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const debugSmoothMap: Move<SwarmsState> = ({ G }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const smoothTerrains = smoothMap(originalTerrains);
  const improvedMap = smoothTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const confirm: (
  G: SwarmsState,
  id: number
) => SwarmsState | "INVALID_MOVE" | undefined = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  const stall = currentPlayer.selection.stall;
  if (slot === undefined || stall === undefined) {
    return invalidMove();
  }
  const action = slot.action;

  const upgradeG = upgradeGameGrid(G, id);

  const runner = getActionRunner(action);
  const actionGame: SwarmsState = runner(upgradeG, id);

  const detailedScore = (G: SwarmsState, player: Player) =>
    scienceScore(player, G.map).map((score, i) => {
      const bpm = blueprintMastery(
        player.grid,
        player.grid[i][Action.Research].blueprint
      );
      return [score, bpm];
    }, resourceScore(player));
  console.log(
    id,
    action,
    currentPlayer.timeSpent,
    ...actionGame.players.flatMap((p) => [
      resourceScore(p),
      ...detailedScore(actionGame, p).flat(),
    ])
  );
  return resetSelection(actionGame, id);
};

const resetSelection = (G: SwarmsState, id: number) =>
  deepAssign(G, noSelection, "players", (p) => p.id === id, "selection");

const discardSelection: Move<SwarmsState> = ({ G, playerID }) =>
  resetSelection(G, parseInt(playerID || "0"));

const distanceToCorner = (id: number, c: Coordinates) => {
  const corner = startCorners()[id];
  return Math.abs(c.col - corner.col) + Math.abs(c.row - corner.row);
};

const sortDistanceToCorner = (id: number, a: Coordinates[], b: Coordinates[]) =>
  a.reduce((acc, aC) => acc + distanceToCorner(id, aC), 0) -
  b.reduce((acc, bC) => acc + distanceToCorner(id, bC), 0);

const getSpreadingPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  (player.swarm.length > 0
    ? getAdjacentToSelection(player.swarm)
    : [startCorners(1)[player.id]]
  )
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateSpreadingBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player.id, a, b));

const getBuildPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  [
    ...player.swarm.flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateBuildBlueprint(map, bp, blueprint, player)
      )
    ),
  ].sort((a, b) => sortDistanceToCorner(player.id, a, b));

const getManufacturePatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  [
    ...player.swarm.flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateManufactureBlueprint(map, bp, blueprint, player)
      )
    ),
  ].sort((a, b) => sortDistanceToCorner(player.id, b, a));

const getDeliveryDestination: (
  map: TerrainMap,
  location: Coordinates[],
  moveUnit: Coordinates,
  blueprint: Blueprint,
  id: number
) => Coordinates[] | undefined = (map, location, moveUnit, blueprint, id) =>
  numberlist(GAME_SIZE).reduce(
    (acc: { last: Coordinates[]; furthest: Coordinates[] | undefined }, _) => {
      const next = acc.last.map((c: Coordinates) =>
        addCoordinates(c, moveUnit)
      );
      return {
        last: next,
        furthest: validateDeliveryBlueprint(map, blueprint, location, next, id)
          ? next
          : acc.furthest,
      };
    },
    { last: location, furthest: undefined }
  ).furthest;

export const SwarmsGame: Game<SwarmsState> = {
  setup: ({ random, ctx }) => {
    const map: TerrainMap = newMap(random.Number);

    const playerOrder = numberlist(ctx.numPlayers);
    const blueprintList = random.Shuffle(BlueprintList);
    const blueprints = [...blueprintList, blueprintList[0]];
    const players: Player[] = playerOrder.map((id) => newPlayer(id));
    const turnTrack: Track = [[...playerOrder]];
    const market = newMarket;
    const gameEndTriggered = undefined;

    const statistics = {};

    const initialPlayerGame = {
      map,
      market,
      players,
      turnTrack,
      gameEndTriggered,
      statistics,
    };

    return playerSetupState(initialPlayerGame, blueprints);
  },

  turn: {
    order: {
      first: ({ G }) => nextPlayer(G.players, G.turnTrack, G.gameEndTriggered),
      next: ({ G }) => nextPlayer(G.players, G.turnTrack, G.gameEndTriggered),
    },
    onEnd: ({ G, random, ctx }) => {
      const id = parseInt(ctx.currentPlayer || "0");
      const currentPlayer = G.players[id];
      const harvestedResources = harvest(G, id);
      const harvestCount = Math.min(
        harvestedResources.length,
        currentPlayer.city.length - currentPlayer.silo.length
      );
      const marketRefill = Math.max(
        1,
        Math.trunc(currentPlayer.city.length / 4) - G.market.stalls.length
      );
      const market = refillMarketTokens(G.market, random.Shuffle, marketRefill);
      const players = G.players
        .map((p) =>
          p.id === id
            ? {
                ...p,
                selection: {
                  ...noSelection,
                  time: harvestCount + 1,
                },
                silo: [...p.silo, ...harvestedResources],
              }
            : p
        )
        .map((p) => sortResources(G, p));
      const statistics = {
        ...G.statistics,
        harvests: (G.statistics.harvests || 0) + 1,
        harvestedResources:
          (G.statistics.harvestedResources || 0) + harvestCount,
      };
      return passTime({ ...G, players, market, statistics }, id);
    },
  },

  moves: {
    draftToken,
    selectSlot,
    selectLocation,
    deselectLocation,
    selectDestination,
    deselectDestination,
    discardSelection,
    passTurn,
    debugRaiseMap,
    debugSmoothMap,
  },

  endIf: ({ G: { map, players, gameEndTriggered } }) => {
    if (gameEndTriggered !== undefined) {
      if (players.every((p) => p.timeSpent >= gameEndTriggered)) {
        return winner(players, map);
      }
    }
  },

  onEnd: ({ G }) => {
    const players = G.players.map((player) => {
      const score = playerScore(G, player);
      return { ...player, score };
    });
    console.log({
      "player scores": players.map((p) => p.score),
      "time consumed": G.gameEndTriggered,
      ...G.statistics,
    });
    return { ...G, players };
  },

  ai: {
    enumerate: (G, _ctx, playerID) => {
      const id = parseInt(playerID || "0");
      const currentPlayer = G.players[id];
      const selection = currentPlayer.selection;
      const stall = selection.stall;
      if (stall === undefined) {
        return [{ move: "draftToken", args: [0] }];
      }
      if (G.market.stalls.length === 0 || currentPlayer.silo.length < 4) {
        return [{ move: "passTurn" }];
      }
      const token = G.market.stalls[stall];
      if (selection.slot === undefined) {
        const moves = [
          ...currentPlayer.grid.flatMap((_, i) =>
            ActionList.filter((action) =>
              isValidActionSelection(
                currentPlayer,
                currentPlayer.grid[i][action],
                G.market.stalls.filter((t) => t === token)
              )
            ).map((action) => ({
              move: "selectSlot",
              args: [i, action],
              order: i + ActionList.findIndex((a) => a === action),
            }))
          ),
        ].sort((a1, a2) => a2.order - a1.order);
        if (moves.length === 0) {
          if (stall >= G.market.stalls.length - 1) {
            return [{ move: "passTurn" }];
          }
          return [{ move: "draftToken", args: [stall + 1] }];
        }
        return moves;
      }
      if (selection.location.length < 4) {
        switch (selection.slot.action) {
          case Action.Spread: {
            const moves = getSpreadingPatterns(G.map, token, currentPlayer)
              .slice(0, 1)
              .map((pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Spread],
              }));
            if (moves.length === 0) {
              if (stall >= G.market.stalls.length - 1) {
                return [{ move: "passTurn" }];
              }
              return [{ move: "draftToken", args: [stall + 1] }];
            }
            return moves;
          }
          case Action.Build: {
            const moves = getBuildPatterns(G.map, token, currentPlayer)
              .slice(0, 1)
              .map((pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Build],
              }));
            if (moves.length === 0) {
              if (stall >= G.market.stalls.length - 1) {
                return [{ move: "passTurn" }];
              }
              return [{ move: "draftToken", args: [stall + 1] }];
            }
            return moves;
          }
          case Action.Trade: {
            const moves = getManufacturePatterns(G.map, token, currentPlayer)
              .filter(
                (pattern) =>
                  VECTOR_ORIENTATIONS.map((moveUnit) =>
                    getDeliveryDestination(G.map, pattern, moveUnit, token, id)
                  ).filter((pattern) => pattern !== undefined).length > 0
              )
              .slice(0, 1)
              .map((pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Trade],
              }));
            if (moves.length === 0) {
              if (stall >= G.market.stalls.length - 1) {
                return [{ move: "passTurn" }];
              }
              return [{ move: "draftToken", args: [stall + 1] }];
            }
            return moves;
          }
          default: {
            if (stall >= G.market.stalls.length - 1) {
              return [{ move: "passTurn" }];
            }
            return [{ move: "draftToken", args: [stall + 1] }];
          }
        }
      }
      if (selection.destination.length < 4) {
        if (selection.slot.action === Action.Trade) {
          const moves = VECTOR_ORIENTATIONS.map((moveUnit) =>
            getDeliveryDestination(
              G.map,
              selection.location,
              moveUnit,
              token,
              id
            )
          )
            .filter((pattern) => pattern !== undefined)
            .map((pattern) => ({
              move: "selectDestination",
              args: [pattern],
            }));
          if (moves.length === 0) {
            if (stall >= G.market.stalls.length - 1) {
              return [{ move: "passTurn" }];
            }
            return [{ move: "draftToken", args: [stall + 1] }];
          }
          return moves;
        }
      }
      if (stall >= G.market.stalls.length - 1) {
        return [{ move: "passTurn" }];
      }
      return [{ move: "draftToken", args: [stall + 1] }];
    },
  },
};

const invalidMove = () => {
  const a = true || false;
  if (a) return INVALID_MOVE;
};
